:root {
  --primary-color-green: #1f4b43;
  --primary-color-green-thick: #1c423b;
  --secondry-font: "manrope";
  --secondary-color-orange: #dba94d;
}
button {
  cursor: pointer;
}
/* new sectio */
.hero {
  width: 100vw;
  height: 100vh;
  background-image: url("../assets/lANDING-PAGE-IMG.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 10%;
}
.hero-text {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
}
.hero_title {
  color: var(--primary-color-green);
  font-size: 3.5vw;
}
.hero_sub_title {
  color: rgba(0, 0, 0, 0.767);
  font-size: 2vw;
}
.hero-cta-btn {
  margin-top: 2%;
}
.hero_cta_btns {
  width: 180px;
  height: 50px;
  border-radius: 10px;
  border-bottom-left-radius: 0;
  border: 2px solid var(--primary-color-green);
  color: var(--primary-color-green);
  font-weight: 900;
  margin: 5px;
  font-family: monospace;
  cursor: pointer;
}
.download_cta_btn {
  background-color: var(--primary-color-green);
  color: white;
  transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
  -webkit-transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
  -moz-transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
  -ms-transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
  -o-transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
}
.learn_more_cta_btn {
  transition: background-color 0.2s ease-out, border-color 0.2s ease-out,
    color 0.2s;
  -webkit-transition: background-color 0.2s ease-out, border-color 0.2s ease-out,
    color 0.2s;
  -moz-transition: background-color 0.2s ease-out, border-color 0.2s ease-out,
    color 0.2s;
  -ms-transition: background-color 0.2s ease-out, border-color 0.2s ease-out,
    color 0.2s;
  -o-transition: background-color 0.2s ease-out, border-color 0.2s ease-out,
    color 0.2s;
}
.download_cta_btn:hover,
.learn_more_cta_btn:hover {
  background-color: var(--secondary-color-orange);
  border: none;
  color: white;
}
/* section ends */
